import React from "react"

import { Element } from "react-scroll"
import { Container } from "reactstrap"
import LandingHeader from "../components/landingHeader"
import Layout from "../components/layout"
import SEO from "../components/seo"

const IndexPage = () => (
  <Layout>
    <SEO />
    <LandingHeader />
    <Element name="contacts" className="section">
      <Container>
        <div className="title">
          <h2>Контакты</h2>
        </div>
      </Container>
      <div
        className="embed-responsive embed-responsive-21by9 mb-5"
        style={{ maxHeight: "480px" }}
      >
        <iframe
          className="embed-responsive-item"
          src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2115.115902695363!2d56.186549316323244!3d57.985870981209175!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x43e8c75410e2de09%3A0x481cb668932acb9a!2sUlitsa+9+Maya%2C+3%2C+Perm%2C+Permskiy+kray%2C+614066!5e0!3m2!1sen!2sru!4v1519581488922"
          width="600"
          height="350"
        />
      </div>
      <Container>
        <ul className="list-unstyled">
          <li className="mb-2">
            <p className="lead">
              <i className="fa fa-map" aria-hidden="true" /> &nbsp; 614066,
              Пермь 9 мая д. 3
            </p>
          </li>
          <li className="mb-2">
            <p className="lead">
              <i className="fa fa-phone" aria-hidden="true" /> &nbsp;
              +7(499)646-05-00
            </p>
          </li>
          <li className="mb-2">
            <p className="lead">
              <i className="fa fa-envelope" aria-hidden="true" /> &nbsp;
              info@flawlesscode.ru
            </p>
          </li>
        </ul>
      </Container>
    </Element>
  </Layout>
)

export default IndexPage
