import React, { FC } from "react"

import classnames from "classnames"
import { graphql, useStaticQuery } from "gatsby"
import { Link } from "react-scroll"
import {
  Collapse,
  Container,
  Nav,
  Navbar,
  NavbarBrand,
  NavItem,
  NavLink,
} from "reactstrap"

interface HeaderProps {
  siteTitle?: string
  defaultNavbarColor?: string
}

const Header: FC<HeaderProps> = ({
  siteTitle = ``,
  defaultNavbarColor = "navbar-transparent",
}) => {
  const data = useStaticQuery(graphql`
    query {
      logo: file(relativePath: { eq: "logo.png" }) {
        childImageSharp {
          original {
            src
          }
        }
      }
    }
  `)

  const [navbarColor, setNavbarColor] = React.useState(defaultNavbarColor)
  const [navbarCollapse, setNavbarCollapse] = React.useState(false)

  const toggleNavbarCollapse = () => {
    setNavbarCollapse(!navbarCollapse)
    document.documentElement.classList.toggle("nav-open")
  }

  React.useEffect(() => {
    const updateNavbarColor = () => {
      if (
        document.documentElement.scrollTop > 299 ||
        document.body.scrollTop > 299
      ) {
        setNavbarColor("")
      } else if (
        document.documentElement.scrollTop < 300 ||
        document.body.scrollTop < 300
      ) {
        setNavbarColor(defaultNavbarColor)
      }
    }

    window.addEventListener("scroll", updateNavbarColor)

    return function cleanup() {
      window.removeEventListener("scroll", updateNavbarColor)
    }
  }, [defaultNavbarColor, setNavbarColor])

  return (
    <Navbar className={classnames("fixed-top", navbarColor)} expand="lg">
      <Container>
        <div className="navbar-translate">
          <NavbarBrand data-placement="bottom" href="/">
            <img
              src={data.logo.childImageSharp.original.src}
              width="30"
              height="30"
              className="d-inline-block align-top"
              style={{ margin: "-5px 12px -5px 0" }}
              alt=""
            />
            {siteTitle}
          </NavbarBrand>
          <button
            aria-expanded={navbarCollapse}
            className={classnames("navbar-toggler navbar-toggler", {
              toggled: navbarCollapse,
            })}
            onClick={toggleNavbarCollapse}
          >
            <span className="navbar-toggler-bar bar1" />
            <span className="navbar-toggler-bar bar2" />
            <span className="navbar-toggler-bar bar3" />
          </button>
        </div>
        <Collapse
          className="justify-content-end"
          navbar={true}
          isOpen={navbarCollapse}
        >
          <Nav navbar={true}>
            <NavItem>
              <Link
                activeClass="active"
                className="nav-link"
                href="#contacts"
                smooth={true}
                spy={true}
                to="contacts"
              >
                <i className="fa fa-address-card" /> Контакты
              </Link>
            </NavItem>
            <NavItem>
              <NavLink
                data-placement="bottom"
                href="https://github.com/flawlesscodeteam"
                target="_blank"
                title="GitHub Team"
              >
                <i className="fa fa-github" />
                <p className="d-lg-none">GitHub</p>
              </NavLink>
            </NavItem>
          </Nav>
        </Collapse>
      </Container>
    </Navbar>
  )
}

export default Header
