import React, { FC } from "react"

import { graphql, useStaticQuery } from "gatsby"
import { Container } from "reactstrap"

const LandingHeader: FC = () => {
  const data = useStaticQuery(graphql`
    query {
      antoineBarres: file(relativePath: { eq: "antoine-barres.jpg" }) {
        childImageSharp {
          original {
            src
          }
        }
      }
      fogLow: file(relativePath: { eq: "fog-low.png" }) {
        childImageSharp {
          original {
            src
          }
        }
      }
      clouds: file(relativePath: { eq: "clouds.png" }) {
        childImageSharp {
          original {
            src
          }
        }
      }
    }
  `)

  return (
    <>
      <div
        className="page-header section-dark"
        style={{
          backgroundImage: `url(${data.antoineBarres.childImageSharp.original.src})`,
        }}
      >
        <div className="filter" />
        <div className="content-center">
          <Container>
            <div className="title-brand">
              <div className="fog-low">
                <img alt="..." src={data.fogLow.childImageSharp.original.src} />
              </div>
              <div className="fog-low right">
                <img alt="..." src={data.fogLow.childImageSharp.original.src} />
              </div>
            </div>
            <h2 className="presentation-subtitle text-center">
              Сайт находится в разработке
            </h2>
          </Container>
        </div>
        <div
          className="moving-clouds"
          style={{
            backgroundImage: `url(${data.clouds.childImageSharp.original.src})`,
          }}
        />
      </div>
    </>
  )
}

export default LandingHeader
